<template>
  <div class="table-container">
    <div class="form-container">
      <h6>交易订单返佣</h6>
      <div class="form">
        <el-select
          v-model="paramsData.orderState"
          @change="getList"
          placeholder="请选择订单状态"
          clearable
        >
          <el-option
            v-for="item in paymentStatusList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <el-input
          v-model="paramsData.orderNo"
          @blur="getList"
          @clear="getList"
          @keyup.enter="getList"
          placeholder="订单编号"
          clearable
        ></el-input>
        <el-date-picker
          v-model="dateList"
          @change="changeDate"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          value-format="YYYY-MM-DD HH:mm:ss"
        >
        </el-date-picker>
      </div>
    </div>
    <el-table :data="pageData.list">
      <el-table-column
        prop="buyTime"
        label="下单时间"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="appName"
        label="产品名称"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="orderNo"
        label="订单编号"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="realPrice"
        label="实付金额"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="amount"
        label="返佣金额"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="orderStateName"
        label="状态"
        align="center"
        width="120px"
      ></el-table-column>
      <template #empty>
        <TableEmpty empty-text="暂无交易订单返佣"></TableEmpty>
      </template>
    </el-table>
    <!--   分页   -->
    <PagePagination
      :pageTotal="pageData.pageTotal"
      :pageSize="pageData.pageSize"
      :pageNumber="pageData.pageNumber"
      @emitPageSizeChange="pageSizeChange"
      @emitPageNumberChange="pageNumberChange"
    ></PagePagination>
  </div>
</template>

<script setup lang="ts">
import { ref, shallowReactive } from "vue";

import { usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import { paymentStatusList } from "@assets/typeStatus";

import { agencyCommissionApi } from "@api/customer-management-api";

const { routeQuery } = useVueRouter();
const dateList = ref([]);
const paramsData = shallowReactive({
  endTime: "",
  startTime: "",
  orderNo: "",
  orderState: "",
  agencyId: routeQuery.value.agencyId,
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: agencyCommissionApi,
  props: {
    pageNumber: "current",
    pageSize: "size",
  },
  searchParams: () => {
    return {
      ...paramsData,
    };
  },
});
// 选择日期
const changeDate = (value) => {
  if (value) {
    paramsData.startTime = value[0];
    paramsData.endTime = value[1];
  } else {
    paramsData.startTime = "";
    paramsData.endTime = "";
    dateList.value = [];
  }
  getList();
};
</script>

<style scoped lang="scss">
.form {
  display: inline-flex;
  justify-content: flex-end;
  > div {
    margin-right: 10px;
  }
  :deep(.el-select) {
    flex: 0 0 200px;
  }
  :deep(.el-input) {
    flex: 0 0 200px;
  }
  :deep(.el-date-editor) {
    flex: 0 0 360px;
  }
}
.table-container {
  padding: 32px 24px 20px;
  h6 {
    font-size: 18px;
    font-weight: 900;
    color: #242934;
    line-height: 1;
  }
}
</style>
